import { ButtonProps, Modal as AntdModal, ModalProps, Button as AntdButton, Menu,Dropdown } from 'antd';
import { FC } from 'react';
import { NavLink, useParams } from 'react-router-dom';
import classNames from 'classnames';
import { IconAdd, IconCreateVideo, IconDelete, IconEdit, IconFilter, IconPageFirst, IconPageLast, IconPageNext, IconPagePrev, IconPlayList, IconShare, IconSorter, IconUpload, IconVideoFiles, IconVideoFormat, IconVideoScreen, IconViews } from "./Icons";
import './UserCenter.scss';
import { observer } from 'mobx-react';
import { userStore } from '../../stores/UserStore';
import { ApplyPage } from '../Publish/Apply';
import { translate } from '../../components/Text';

const { SubMenu } = Menu;

export function formatSize(size: number) {
  return formatCount(size, ['b', 'K', 'M', 'G', 'T'], 1024);
}

export function formatCount(count: number, units = ['', 'K', 'M', 'B'], step = 1000) {
  const calc = (size: number, time = 0): number => size < step ? time : calc(size / step, time + 1);
  const lev = calc(Math.max(0, count));
  return Math.round(count / Math.pow(step, lev)).toString() + units[lev];
}

export const Header: FC<{ onAdd?: () => void; addBtn?: any }> = observer(({ children, onAdd, addBtn }) => {
  const { id } = useParams<{ id: string }>();
  return (
    <div className="user-center">
      {userStore.userInfo?.identity == 'creator' ? <>
        <header>
          <div className="nav-area">
            <NavLink to={`/main/userCenter/${id}/myChannel/videos`} className="nav-item" activeClassName="active">
              {translate('creator.common.1')}
            </NavLink>
            <NavLink to={`/main/userCenter/${id}/myChannel/playlist`} className="nav-item" activeClassName="active">
              {translate('creator.common.2')}
            </NavLink>
            <NavLink to={`/main/userCenter/${id}/myCenter/followers`} className="nav-item" activeClassName="active">
              {translate('creator.common.5')}
            </NavLink>
          </div>
          {addBtn && <button className="create-btn" onClick={onAdd}>{addBtn}</button>}
        </header>
        {children}
      </> : <ApplyPage />}
    </div>
  );
});


export const MyCenterHeader: FC<{}> = observer(({ children }) => {
  const user = userStore.userInfo;
  const { id } = useParams<{ id: string }>();
  if (!user) {
    return <div />;
  }
  // TODO: My Center Header
  return (
    <Header>
      <div className="my-center-header">
        <div className="header-bg" />
        <div className="user-info-area-wrap">
          <div className="user-info-area">
            <img className="avatar" src={user.avatar} />
            <div className="info-area">
              <h1>{user.username}</h1>
              <div className="other-area">
                <div className="user-center-nav">
                  <NavLink to={`/main/userCenter/${id}/myCenter/followers`} activeClassName="active">Followers</NavLink>
                  <NavLink to={`/main/userCenter/${id}/myCenter/following`} activeClassName="active">Following</NavLink>
                </div>
                <div className="number-area">
                  <div className="number-item">
                    <span className="number-num">{formatCount(parseInt(user.fanscount, 10))}</span>
                    <span className="gray">Followers</span>
                  </div>
                  <div className="number-item">
                    <span className="number-num">{formatCount(parseInt(user.followcount, 10))}</span>
                    <span className="gray">Following</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {children}
      </div>
    </Header>
  );
});

export interface ToolbarProps{
  changeModel:Function;
  changeSort:Function;
}

export const ListToolBar = observer((props:ToolbarProps) => {
  const menu = (
    <Menu>
      <Menu.Item onClick={()=>{
        props.changeModel('time')
      }}>
        <a target="_blank" rel="noopener noreferrer" href="https://www.antgroup.com">
          {translate('creator.center.1')}
        </a>
      </Menu.Item>
      <Menu.Item onClick={()=>{
        props.changeModel('hot')
      }}>
        <a target="_blank" rel="noopener noreferrer" href="https://www.aliyun.com">
          {translate('creator.center.2')}
        </a>
      </Menu.Item>
    </Menu>
  );
  return (
    <div className="list-toolbar">
      <Dropdown overlay={menu} placement="bottomCenter" arrow={false}>
      <a className="filter-btn">
        {IconFilter}  {translate('creator.center.3')}
      </a>
      </Dropdown>
     
      <a className="sort-btn">
        {IconSorter}
      </a>
    </div>
  );
})

export interface ListPagenationProps {
  pageNum: number;
  pageSize: number;
  total: number;
  onChange: (pageNumb: number) => void;
}

export const ListPagenation: FC<ListPagenationProps> = ({ pageNum, pageSize, total, onChange }) => {
  const totalPage = Math.ceil(total / pageSize);
  return (
    <div className="list-pagenation">
      <div className="info">1 - 4 of 6</div>
      <div className="pages">
        <a className="active">1</a>
        <a>2</a>
      </div>
      <div className="btns">
        <a>{IconPageFirst}</a>
        <a>{IconPagePrev}</a>
        <a>{IconPageNext}</a>
        <a>{IconPageLast}</a>
      </div>
    </div>
  );
};

export const Modal: FC<ModalProps> = ({ wrapClassName, children, ...rest }) => {
  return (
    <AntdModal {...rest}
      wrapClassName={classNames("creator-modal", wrapClassName)}
      footer={null}
      maskClosable={false}
    >
      {children}
    </AntdModal>
  )
};

export const Button: FC<ButtonProps> = ({ className, ...rest }) => {
  return (
    <AntdButton {...rest} className={classNames('creator-button', className)} />
  );
}