import { Form, Input, Button } from 'antd';
import { observer } from 'mobx-react';
import { publishStore } from '../../stores/PublishStore';
import { userStore } from '../../stores/UserStore';
import './Apply.scss';
const FormItem = Form.Item;
const { TextArea } = Input;

const txtPlaceHolder = `1. 内容完整通顺，无特殊符号
2. 展示个人特色，包括个人经历、擅长领域等`;

export const ApplyPage = observer(() => {

  const doSubmit = (values: any) => {
    publishStore.apply(values);
  };
  return (
    <div className="creator-apply-page">
      <h1>欢迎来到Oli创作者平台</h1>
      {(userStore.userInfo?.applyCreatorStatus == 0 || publishStore.reviewing) && <div className="success-view">
        <h2>您的创作者申请已提交</h2>
        <div className="success-desc">接下来我们将对您提交的信息进行审核，在审核完成后，您可上传发布视频，资讯和帖子，请您耐心等待我们这边的审核结果</div>
      </div>}
      {userStore.userInfo?.applyCreatorStatus == -1 && <Form className="form" labelAlign="left" onFinish={doSubmit}>
        <FormItem label="用户名" name="name" required>
          <Input placeholder="2～10个字，请勿使用含特殊符号或明显营销推广意图的媒体名" />
        </FormItem>
        <FormItem label="联系邮箱" name="email" required>
          <Input placeholder="请输入您的邮箱地址，方便我们这边与您联系" />
        </FormItem>
        <FormItem label="联系电话" name="phone" required>
          <Input placeholder="请输入您的电话号码，方便我们这边与您联系" />
        </FormItem>
        <FormItem label="简介" name="describe" required>
          <TextArea placeholder={txtPlaceHolder} />
        </FormItem>
        <FormItem label=" ">
          <Button htmlType="submit" loading={publishStore.loading} className="submit-btn">提交</Button>
        </FormItem>
      </Form>}
    </div>
  )
});