import { ReactNode, useEffect, useState } from 'react';
import { Row, Col } from 'antd';

interface GridViewProps {
    id: string
    gutter?:any
    itemCount: number
    itemBuilder: (i: number) => ReactNode
}

export const GridView = (props: GridViewProps) => {
    const [subArray, setSubArray] = useState([] as any)
    useEffect(() => {
        let row: any = document.getElementById(props.id)
        let cols: any = document.getElementsByClassName(props.id + 0);
        let colCount = Math.floor(row.clientWidth / cols[0].clientWidth);
        let subCount = props.itemCount % colCount;
        if (subCount) {
            setSubArray(new Array(colCount - subCount));
        }
    }, [props]);
    return (
        <Row id={props.id} justify="center" gutter={props.gutter || [10,10]}>
            {[...new Array(props.itemCount), ...subArray].map((it: any, idx: number) => {
                if (idx >= props.itemCount) {
                    return <Col key={idx} style={{ visibility: 'hidden' }}>{props.itemBuilder(0)}</Col>
                }
                return (
                    <Col key={idx} className={props.id + idx}>
                        {props.itemBuilder(idx)}
                    </Col>
                );
            })}
        </Row>
    )
}