import { FC, useEffect, useState } from "react";
import { Button, formatSize, Modal } from "./Common";
import { IconAdd, IconAdd2, IconDelete, IconPause, IconUpload, IconUploading, IconUploadSuccess, IconVideoFiles, IconVideoFormat, IconVideoScreen } from "./Icons";
import { message, Upload } from 'antd';
import classNames from 'classnames';
import { useUploaderList, Uploader, useUploader, useUploaderProgress } from "../../utils/upload";
import { ImageUploader } from "../../components/ImageUploader";
import { useRequest } from "../../utils/hooks";
import { PublishVideoService } from "../../services/PublishService";
import { showError, showSuccess } from "../../components/Message";

const { Dragger } = Upload;



function formatFileName(fileName: string) {
  const m = /^(.+)\.([\w]+)$/.exec(fileName);
  if (m) {
    return `${m[1].slice(0, 7)}...${m[2]}`;
  } else {
    return `${fileName.slice(0, 10)}...`;
  }
}

const VideoUploadingItem: FC<{ className?: string; uploader: Uploader; onCancel: () => void; onSelect: () => void }> = ({ className, uploader, onCancel, onSelect }) => {
  const { status, pause, resume } = useUploader(uploader);
  const { total, percent } = useUploaderProgress(uploader);
  const percentTxt = `${Math.ceil(percent * 100)}%`;
  let statusCentent;
  if (status === 'init') {
    statusCentent = (
      <>
        {IconUploading}
        <div className="info">Waiting...</div>
      </>
    );
  } else if (status === 'uploading') {
    statusCentent = (
      <>
        {IconUploading}
        <div className="info">Uploading...{percentTxt}</div>
      </>
    );
  } else if (status === 'success') {
    statusCentent = (
      <>
        {IconUploadSuccess}
        <div className="info">Ready for Publish</div>
      </>
    );
  }
  return (
    <div className={classNames('upload-list-item', className)}>
      <div className="up-area" onClick={onSelect}>
        <div className="status">
          Editing Details...
        </div>
        {statusCentent}
      </div>
      <div className="down-area">
        <div className="file-info">
          <span className="file-name">{formatFileName(uploader.fileName)}</span>
          {total > 0 && <span className="file-size">{formatSize(total)}</span>}
        </div>
        <div className="upload-progress">
          <div style={{ width: percentTxt }} />
        </div>
        <div className="btns">
          {/* <a>
            {IconPause}
            <span>Pause</span>
          </a> */}
          <a onClick={onCancel}>
            {IconDelete}
            <span>Cancel</span>
          </a>
        </div>
      </div>
    </div>
  );
};

type UploaderListState = ReturnType<typeof useUploaderList>;

const VideoUploaderAndList: FC<{ current?: Uploader; state: UploaderListState; onSelect: (it: Uploader) => void }> = ({ current, state, onSelect }) => {
  const { list, startUpload, remove } = state;
  return (
    <div className="video-uploader">
      {list.length <= 0 ? (
        <Dragger name="file" accept=".mp4" multiple={true} beforeUpload={startUpload} itemRender={() => <div />}>
          <div className="new-uploader">
            <div className="icon-wrap">{IconUpload}</div>
            <div className="title">Drag and drop video files to upload</div>
            <div className="desc">Your videos will be private until you publish them</div>
            <Button type="primary">Select Files</Button>
          </div>
        </Dragger>
      ) : (
        <div className="upload-list">
          <div className="append-uploader">
            <Dragger name="file" accept=".mp4" multiple={true} beforeUpload={startUpload} itemRender={() => <div />}>
              {IconAdd}
              <span>Add More</span>
            </Dragger>
          </div>
          {list.map((uploader, i) => (
            <VideoUploadingItem uploader={uploader} key={i} className={current?.id === uploader.id ? 'active' : ''}
              onSelect={() => onSelect(uploader)}
              onCancel={() => remove(uploader)} />
          ))}
        </div>
      )}
    </div>
  );
};

function submitForm(val: any) {
  return new PublishVideoService(val)
    .send()
    .then(() => {
      showSuccess('视频发布完成');
    })
    .catch((err) => {
      message.error(err);
      return Promise.reject(err);
    });
}

export const VideoFormModal: FC<{ collectid?: string; onClose: () => void; onPublish: () => void; }> = ({ onClose, onPublish, collectid }) => {
  const uploaderListState = useUploaderList();
  const [curId, setCurId] = useState<string>();
  const cur = uploaderListState.list.find(it => it.id === curId) ?? uploaderListState.list[0];
  const [formData, setFormData] = useState({} as Record<string, any>);
  const { loading, loadWithPromise: submit } = useRequest(submitForm, { autoLoad: false });
  const doSubmit = () => {
    if (!cur || cur.status !== 'success') {
      showError('上传未完成!');
      return;
    }
    submit({
      ...getValues(),
      alivideoid: cur.videoId,
      url: cur.url,
      collectid
    }).then(() => {
      uploaderListState.remove(cur);
      onPublish();
    });
  };
  const getValues = () => {
    if (!cur) return '';
    return formData[cur.id] ?? {};
  }
  const field = (name: string) => [
    getValues()[name] ?? '',
    (val: any) => {
      if (!cur) return;
      setFormData({ ...formData, [cur.id]: { ...getValues(), [name]: val } });
    }
  ];
  const fTitle = field('title');
  const fDesc = field('abstract');
  const fPrice = field('price');
  const fThumb = field('thumb');

  return (
    <Modal className="creator-video-upload-modal" width="72.25rem" title={`Upload Videos ${collectid ? 'to Play list' : ''}`} visible={true} onCancel={onClose}>
      <div className="intro-area">
        <div className="intro-item">
          {IconVideoFormat}
          <div>
            <div className="title">Video Format</div>
            <div className="desc gray">Recommend 16:9, 18:9, 21:9 Widescreen</div>
          </div>
        </div>
        <div className="intro-item">
          {IconVideoScreen}
          <div>
            <div className="title">Video Resolution</div>
            <div className="desc gray">Recommend &gt; 1920*1080</div>
          </div>
        </div>
        <div className="intro-item">
          {IconVideoFiles}
          <div>
            <div className="title">Video File Size</div>
            <div className="desc gray">File size &lt; 32G</div>
          </div>
        </div>
      </div>
      <VideoUploaderAndList state={uploaderListState} current={cur} onSelect={(it) => setCurId(it.id)} />
      {cur && (<>
        <div className="c-form">
          <div className="c-form-left">
            <h1>Details</h1>
            <div className="desc">Add Descriptions of this video for your Viewers</div>
            <div className="input-field">
              <label>Title (Required)</label>
              <input value={fTitle[0]} onChange={e => fTitle[1](e.target.value)} />
            </div>
            <div className="input-field">
              <label>Descriptions</label>
              <textarea value={fDesc[0]} onChange={e => fDesc[1](e.target.value)} />
            </div>
          </div>
          <div className="c-form-right">
            <h1>Select/Upload Your Thumbnail</h1>
            <div className="desc">Select or upload a picture that shows what's in your video. A good thumbnail stands out and draws viewers' attention</div>
            <div className="input-field">
              <label>Choose From Video or upload</label>
              <ImageUploader className="thumb-uploader" width={240} height={135} 
                value={fThumb[0]}
                onChange={fThumb[1]}
                displayScale={.5}
                displayContent={(
                <div className="disp">
                  {IconAdd2}
                  <span>Upload</span>
                </div>
              )} />
            </div>
            {!collectid && <>
            <h1>Price</h1>
            <div className="desc">Explain how the Price system works in</div>
            <div className="input-field2">
              <label>Set Price $1-999</label>
              <input value={fPrice[0]} onChange={e => fPrice[1](e.target.value)} />
            </div>
            </>}
          </div>
        </div>
        <div className="btn-bar">
          <Button onClick={doSubmit} loading={loading}>Publish Now</Button>
        </div>
      </>)}
    </Modal>
  );
}