import { FC, useEffect, useState } from "react";
import { NavLink, useHistory, useParams } from "react-router-dom";
import { ButtonProps, Modal as AntdModal, ModalProps, Button as AntdButton, Upload, Popconfirm } from 'antd';
import { IconAdd, IconCreateVideo, IconDelete, IconEdit, IconFilter, IconPageFirst, IconPageLast, IconPageNext, IconPagePrev, IconPlayList, IconShare, IconSorter, IconUpload, IconVideoFiles, IconVideoFormat, IconVideoScreen, IconViews } from "./Icons";

import './UserCenter.scss';
import { Button, Header, ListPagenation, ListToolBar, Modal } from "./Common";
import { CreatorVideoDeleteService, CreatorVideoItem, CreatorVideoService } from "../../services/CreatorVideoService";
import { useRequest } from "../../utils/hooks";
import { VideoFormModal } from "./VideoFormModal";
import { Loading } from "../../components/Loading";
import NoData from "../../components/NoData";
import { VidoeEditModal } from "../CreatorCenter/VideoEditorModal";


const getVideosData = ({ id = '', pageNum = 1, pageSize = 999 }) => new CreatorVideoService({ p: pageNum, psize: pageSize, userid: id }).send();

const deleteVideo = ({ id = '' }) => new CreatorVideoDeleteService({ objectid: id, objecttype: 2 }).send();

export const CreatorCenterMyChannelVideos: FC<{}> = () => {
  const { id } = useParams<any>();
  const { loading, data = [], load } = useRequest(getVideosData, { autoLoad: false });
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [curEditItem, setCurEditItem] = useState<CreatorVideoItem>();
  const his = useHistory();
  useEffect(() => load({ id }), []);
  return (
    <Header onAdd={() => setShowCreateModal(true)} addBtn={<>{IconCreateVideo} Upload Video</>}>
      <div className="videos-tools">
        <ListToolBar changeModel={() => {

        }} changeSort={() => {

        }} />
      </div>
      <div className="videos-list">
        {loading ? <Loading size="mini" /> : !data.length ? <NoData /> : data.map((it, i) => {
          const doJump = () => his.push(`/main/videoPlay/ua${id}/${it.id}`);
          return (
            <div key={i} className="video-item">
              <div className="thumb" onClick={doJump}>
                <img src={it.thumb} />
                <div className="time-length">20:18</div>
              </div>
              <div className="video-info">
                <div className="title" onClick={doJump}>{it.title}</div>
                <div className="info" onClick={doJump}>
                  <div className="time">
                    <div className="gray">Uploaded on</div>
                    <div>{it.createtime.slice(0,16)}</div>
                  </div>
                  <div className="gray">{it.viewcount} Views</div>
                </div>
                <div className="desc gray">
                  {it.abstract}
                </div>
                <div className="btns">
                  <div className="fee-btn">{parseInt(it.price) > 0 ? it.price : 'FREE'}</div>
                  <div className="other-btns">
                    <a className="btn" onClick={() => setCurEditItem(it)}>{IconEdit}</a>
                    <Popconfirm title="确定删除?" onConfirm={() => deleteVideo({ id: it.id }).then(() => load({ id }))}>
                      <a className="btn">{IconDelete}</a>
                    </Popconfirm>
                    {/* <a className="btn">{IconViews}</a>
                    <a className="btn">{IconShare}</a> */}
                  </div>
                </div>
              </div>
            </div>
          );
        })}
      </div>
      <div className="center">
        {/* <ListPagenation {...state} onChange={state.load} /> */}
      </div>
      {showCreateModal && <VideoFormModal onClose={() => setShowCreateModal(false)} onPublish={() => load({ id })} />}
      <VidoeEditModal
        visible={!!curEditItem}
        currentItem={curEditItem}
        onComplete={() => load({ id })}
        onClose={() => setCurEditItem(undefined)} />
    </Header>
  );
};
