import { Popconfirm } from "antd";
import { useHistory } from "react-router";
import { GridView } from "../../components/GridView";
import { Image } from "../../components/Image";
import NoData from "../../components/NoData";
import type { CollectionItem } from "../../services/CollectionListService";
import { IconPlayList } from "../UserCenter/Icons";
import './VideoCollectionList.scss';

export interface VideoCollectionListProps {
  list: CollectionItem[];
  onClick?: (item: CollectionItem) => void;
  onEdit?: (item: CollectionItem) => void;
  onDelete?: (item: CollectionItem) => void;
}

export function VideoCollectionList({ list, onClick, onEdit, onDelete }: VideoCollectionListProps) {
  const his = useHistory();
  const doClick = onClick ?? ((item: CollectionItem) => his.push(`/main/creatorCollectionDetail?id=${item.id}&title=${encodeURIComponent(item.name)}`));
  if (!list.length) {
    return <div className="video-collection-no-data"><NoData text="此频道中没有任何播放列表" /></div>;
  }

  return (
    <div className="video-collection-list">
      <GridView id="collectionList" gutter={[13, 15]} itemCount={list.length} itemBuilder={(idx) => {
        let it: any = list[idx];
        let price = it.lookauth === 1 || it.islookauth === 1 ? '0' : it.price
        return (
          <div className='video-item' key={it.id} >
            {/* <Image className="thumb" src={it.cover} /> */}
            <div className="thumb" onClick={() => doClick(it)}>
              <img src={it.cover} />
              <div className="thumb-info">
                <span>{it.videocount} Videos</span>
                {IconPlayList}
              </div>
            </div>
            <div className="info">
              <img className="avatar" src={it.avatar} onClick={() => his.push(`/main/userCenter/${it.userid}/videoList`)} />
              <div>
                <div className="title">{it.name}</div>
                <div className="author">{it.username}</div>
              </div>
            </div>
            {price != '0' ? <div className="price">${it.price}</div> : <></>}
          </div>
        );
      }} />
      {/* {list.map((it) => (
        <div className="video-collection-item" key={it.id}>
          <Image className="thumb" src={it.cover} alt={it.cover} onClick={() => doClick(it)} />
          <div className="info">
            <div className="title">{it.name}</div>
            
            <div className="count">
            {onEdit && <a onClick={() => onEdit(it)}>{Icon_Edit}</a>}
            {onDelete && (
              <Popconfirm
                placement="topRight"
                title="确认删除?"
                onConfirm={() => onDelete(it)}
                okText="是"
                cancelText="否"
              >
                <a>{Icon_Delete}</a>
              </Popconfirm>
            )}
              <svg width="14" height="12" viewBox="0 0 14 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M0.867201 9.84335C0.867201 10.5958 1.47724 11.2066 2.23042 11.2066H11.773C12.5255 11.2066 13.1362 10.5958 13.1362 9.84335V1.664C13.1362 0.910824 12.5255 0.300781 11.773 0.300781H2.23041C1.47723 0.300781 0.867188 0.910824 0.867188 1.664V9.84335H0.867201ZM10.4098 5.75367L4.95687 9.16173V2.34561L10.4098 5.75367Z" fill="#999999"/>
              </svg>
              <span>{it.videocount}</span>
            </div>
          </div>
        </div>
      ))} */}
    </div>
  );
}