import { FC, useEffect, useState } from "react";
import { useHistory } from "react-router";
import classNames from "classnames";
import { Loading } from "../../components/Loading";
import NoData from "../../components/NoData";
import { CollectionListService } from "../../services/CollectionListService";
import { CreatorUserListService } from "../../services/CreatorService";
import { useCurrentUser, useQuery, useRequest, useRequestLoadMore, useScrollToBottomEvent, useVideoSearch } from "../../utils/hooks";
import "./CreatorCenter.scss";
import { CreatorList } from "./CreatorList";
import { VideoCollectionList } from "./VideoCollectionList";
import { getLabels, LabelItem } from "./VideoEditorModal";
import { VideoData, VideoService } from "../../services/VideoService";
import moment from "moment";
import Skeleton, { SkeletonTheme } from 'react-loading-skeleton';
import { Image } from "../../components/Image";
import { StripePayArea } from "../Stripe/Stripe";
import { Carousel } from 'antd';
import { GridView } from "../../components/GridView";
import GlobalConfig from "../../config/GlobalConfig";
import { IndexBannerService } from "../../services/IndexBannerService";
import { translate } from "../../components/Text";
import { BannerLoader, LableLoader, VideoLoader } from "../Skelleton";
import { observer } from "mobx-react";
export { IconSubCount } from "./CreatorList";

export const ViewBlock: FC<{
  title: string;
  loading?: boolean;
  noData?: boolean;
  noDataTxt?: string;
  onMore?: () => void;
}> = (props) => {
  return (
    <div className="block-view">
      <h1 className="block-header">
        <span>{props.title}</span>
        {props.onMore && (
          <a className="more-btn" onClick={props.onMore}>
            更多
          </a>
        )}
      </h1>
      {props.loading ? <Loading size="mini" /> : props.noData ? <NoData text={props.noDataTxt} /> : props.children}
    </div>
  );
};

const HotCollectionsArea = () => {
  const his = useHistory();
  const { loading, data = [] } = useRequest(() =>
    new CollectionListService({ ishot: 1, psize: 5 }).send()
  );
  return loading ? <GridView id="videoList" gutter={[13, 15]} itemCount={5} itemBuilder={(idx) => <VideoLoader />} /> : (
    <div className="vidoe-coll-area">
      <VideoCollectionList list={data}
        onClick={(it) => his.push(`/main/videoPlay/c${it.id}/0`)}
      />
    </div>
  );
};

const AllCollectionsArea = () => {
  const his = useHistory();
  const { reloading, data = [], reload, moreLoad } = useRequestLoadMore(({ pageNum, pageSize }) =>
    new CollectionListService({ p: pageNum, psize: pageSize }).send()
  );
  useEffect(() => reload({}), []);
  useScrollToBottomEvent(moreLoad);
  return reloading ? <GridView id="videoList" gutter={[13, 15]} itemCount={20} itemBuilder={(idx) => <VideoLoader />} /> : (
    <div className="vidoe-coll-area">
      <VideoCollectionList list={data}
        onClick={(it) => his.push(`/main/videoPlay/c${it.id}/0`)}
      />
    </div>
  );
};

export function getAgo(time: string) {
  return moment(time, 'YYYY-MM-DD HH:mm:ss').format('YYYY-MM-DD');
}


const commonNavItems: LabelItem[] = [
  { name: "推荐", id: "recommend" },
  { name: "合集", id: "collections" },
];

const CommonVideoList = (props: { list: VideoData[]; loading: boolean; onClick: (it: VideoData) => void }) => {
  const his = useHistory();
  const user = useCurrentUser();
  return (
    <div className="label-video-list">
      {props.loading ? <GridView id="videoList" gutter={[13, 15]} itemCount={20} itemBuilder={(idx) => <VideoLoader />} /> : props.list.length === 0 ? <NoData text="此频道中没有任何视频" /> :
        <GridView id="videoList" gutter={[13, 15]} itemCount={props.list.length} itemBuilder={(idx) => {
          let it: any = props.list[idx];
          let price = it.lookauth === 1 || it.islookauth === 1 || it.userid === user?.id ? '0' : it.price
          return (
            <div className='video-item' key={it.id}>
              <StripePayArea
                price={price}
                objectid={it.id}
                ordertype={3}
                targetInfo={{ title: it.title, avatar: it.avatar, subTitle: it.username }}
                onClick={() => props.onClick(it)}
                onComplete={() => props.onClick(it)}
              >
                <Image className="thumb" src={it.thumb} />
              </StripePayArea>
              <div className="info" onClick={() => {
                GlobalConfig.history.push(`/main/userCenter/${it.userid}/videoList`)
              }}>
                <img className="avatar" src={it.avatar} onClick={() => his.push(`/main/userCenter/${it.userid}/videoList`)} />
                <div>
                  <div className="title">{it.title}</div>
                  <div className="author">{it.username}</div>
                  <div className="subInfo">{it.viewcount}次播放 · {it.createtime.slice(0,16)}</div>
                </div>
              </div>
              {price != '0' ? <div className="price">${it.price}</div> : <></>}
            </div>
          );
        }} />
      }
    </div>
  );
};

const LabelVideoList = ({ labelId = "" }) => {
  const his = useHistory();
  const { reload, moreLoad, reloading, data = [] } = useRequestLoadMore(
    ({ pageNum, pageSize, labelid = '' }) => new VideoService(pageNum, pageSize, { labelid }).send(),
    { autoLoad: false }
  );
  useEffect(() => reload({ labelid: labelId }), [labelId]);
  useScrollToBottomEvent(moreLoad);
  return (
    <CommonVideoList
      list={data}
      loading={reloading}
      onClick={(it) => his.push(`/main/videoPlay/l${labelId}/${it.id}`)}
    />
  );
};

const AllVideoList = () => {
  const his = useHistory();
  const { data = [], reloading, reload, moreLoad } = useRequestLoadMore(({ pageNum, pageSize }) => new VideoService(pageNum, pageSize).send());
  useEffect(() => reload({}), []);
  useScrollToBottomEvent(moreLoad);
  return (
    <CommonVideoList
      list={data as any[]}
      loading={reloading}
      onClick={(it) => his.push(`/main/videoPlay/hot/${it.id}`)}
    />
  );
};

export const CreatorCenterPage = observer(() => {
  const his = useHistory();
  const { data = [], loading } = useRequest(() => Promise.all([
    getLabels(),
    new IndexBannerService().send()
  ]));
  const [labels = [], bannerData = []] = data;
  const [curNavItem, setCurNavItem] = useState<LabelItem>(commonNavItems[0]);
  useVideoSearch((value: any) => his.push(`/main/videoSearch?searchKey=${encodeURIComponent(value)}`));

  return (
    <div className="creator-center-page">
        <>
          <div className="nav-area">
            {loading && <GridView id="lable" itemCount={5} itemBuilder={() => <LableLoader />} />}
            {!loading && commonNavItems.concat(labels ?? []).map((label) => (
              <a
                className={classNames("nav-item", {
                  active: curNavItem === label,
                })}
                key={label.id}
                onClick={() => setCurNavItem(label)}
              >
                {label.name}
              </a>
            ))}
          </div>
          <div className="banner-area">
            {loading ? <BannerLoader /> :
              <Carousel autoplay>
                {
                  bannerData.map((b, i) => <img key={i} src={b.url} alt="" />)
                }
              </Carousel>}
          </div>
          <div className="v-box">
            {(() => {
              if (curNavItem.id === "recommend") {

                return (
                  <div className="recommend-area">
                    <h1>{translate('creator.common.3')}</h1>
                    <HotCollectionsArea />
                    <h1>{translate('creator.common.4')}</h1>
                    <AllVideoList />
                  </div>
                );
              } else if (curNavItem.id === "collections") {
                return <AllCollectionsArea />;
              } else {
                return <LabelVideoList labelId={curNavItem.id} />;
              }
            })()}
          </div>
        </>
    </div>
  );
});

export const CreatorVideoSearchPage = () => {
  const his = useHistory();
  const { searchKey = '' } = useQuery();
  const [tab, setTab] = useState('视频');
  const { data: videoResult = [], load: videoLoad, loading: videoLoading } = useRequest((searchkey) => new VideoService(1, 999, { searchkey }).send(), { autoLoad: false });
  const { data: collResult = [], load: collLoad, loading: collLoading } = useRequest((searchkey) => new CollectionListService({ searchkey, psize: 999 }).send(), { autoLoad: false });
  const { data: userResult = [], load: userLoad, loading: userLoading } = useRequest((searchkey) => new CreatorUserListService({ searchkey }).send().then(res => res.list));

  useVideoSearch((value: any) => his.push(`/main/videoSearch?searchKey=${encodeURIComponent(value)}`))

  useEffect(() => {
    const search = decodeURIComponent(searchKey);
    if (tab === '视频') {
      videoLoad(search);
    } else if (tab === '合集') {
      collLoad(search);
    } else {
      userLoad(search);
    }
  }, [searchKey, tab]);

  return (
    <div className="video-search-page v-box">
      <div className="nav-area">
        {['视频', '合集', '创作者'].map(k => (
          <a className={classNames("nav-item", { active: k === tab })} onClick={() => setTab(k)}>{k}</a>
        ))}
      </div>
      {tab === '视频' && (
        <CommonVideoList list={videoResult as any} loading={videoLoading} onClick={it => his.push(`/main/videoPlay/search/${it.id}?searchKey=${searchKey}`)} />
      )}
      {tab === '合集' && (
        collLoading ? <Loading size="mini" /> : collResult.length === 0 ? <NoData text="此频道中没有任何播放列表" /> : <VideoCollectionList list={collResult}
          onClick={(it) => his.push(`/main/videoPlay/c${it.id}/0`)}
        />
      )}
      {tab === '创作者' && (
        userLoading ? <Loading size="mini" /> : userResult.length === 0 ? <NoData text="此频道中没有任何视频" /> : <CreatorList list={userResult} />
      )}
    </div>
  );
}