import { Popconfirm } from "antd";
import { FC, useEffect, useState } from "react";
import { useParams } from "react-router";
import { Loading } from "../../components/Loading";
import { showError } from "../../components/Message";
import NoData from "../../components/NoData";
import GlobalConfig from "../../config/GlobalConfig";
import { CollectionListService } from "../../services/CollectionListService";
import { CreateDelCollectService } from "../../services/PublishService";
import { useRequest } from "../../utils/hooks";
import { VidoeCollectEditModel } from "../CreatorCenter/VideoEditorModal";
import { VideoCollectForm } from "../Publish/PublishVideo";
import { Header, ListPagenation, ListToolBar, Modal } from "./Common";
import { IconDelete, IconEdit, IconPlayList } from "./Icons";

import './UserCenter.scss';

const getCollListData = ({ id = '' }) => new CollectionListService({ psize: 999, userid: id }).send();
const delColl = ({ id = '' }) => new CreateDelCollectService({ id }).send();

export const CreatorCenterPlayList: FC<{}> = () => {
  const { id } = useParams<any>();
  const { loading, data = [], load } = useRequest(getCollListData, { autoLoad: false });
  const [visibleModal, setVisibleModel] = useState(false);
  useEffect(() => load({ id }), []);
  return (
    <Header addBtn="Create Playlist" onAdd={() => setVisibleModel(true)}>
      <div className="play-list">
        <div className="list-header">
          <div className="item-thumb">
            <ListToolBar changeModel={()=>{}} changeSort={()=>{}}/>
            {/* <ListToolBar /> */}
          </div>
          <div className="item-info">
            List Detail
          </div>
          <div className="item-counts">
            Likes/Collected
          </div>
          <div className="item-views">
            Views
          </div>
          <div className="item-operations">

          </div>
        </div>
        {loading ? <Loading size="mini" /> : !data.length ? <NoData /> : data.map((it) => (
          <div className="list-item" key={it.id} onClick={() => {
            GlobalConfig.history.push(`/main/userCenter/${id}/myChannel/playlistDetails?id=${it.id}&title=${encodeURIComponent(it.name)}`);
          }}>
            <div className="thumb">
              <img src={it.cover} />
              <div className="thumb-info">
                <span>{it.videocount} Videos</span>
                {IconPlayList}
              </div>
            </div>
            <div className="item-info">
              <div className="title">{it.name}</div>
              <div className="intro">
                <div className="gray">Uploaded on {it.createtime}</div>
                <div className="fee-btn">{parseInt(it.price, 10) > 0 ? it.price : 'FREE'}</div>
              </div>
              <div className="desc gray">
                {it.describe}
              </div>
            </div>
            <div className="item-counts gray">
              {it.trycount}
            </div>
            <div className="item-views gray">
              3.3M
            </div>
            <div className="item-operations" onClick={e => e.stopPropagation()}>
              <Popconfirm title="确定删除?" onConfirm={() => delColl({ id: it.id }).then(() => load({ id })).catch(showError)}>
                <a className="remove-btn">{IconDelete}</a>
              </Popconfirm>
            </div>
          </div>
        ))}
      </div>
      <div className="center">
        {/* <ListPagenation /> */}
      </div>
      <VidoeCollectEditModel visible={visibleModal} onClose={() => setVisibleModel(false)} onComplete={() => load({ id })} />
    </Header>
  );
};